import { BuildContext, Component, HtmlElement } from "@equantic/runtime";
import { List } from "./List";

export class Text extends HtmlElement {
    constructor(content: any) {
        super();
        this.content = content;
    }

    render() {
        return { tag: this.paragraph ? 'p' : 'span', attributes: buildAttributes(), events: buildEvents(), children: [{ tag: '#text', textContent: this.content }] };
    }

}

