import { BuildContext, Component, HtmlElement } from "@equantic/runtime";
import { List } from "./List";

export class Heading extends HtmlElement {
    constructor(content: any, level: any) {
        super();
        this.content = content;
        this.level = level;
    }

    render() {
        return { tag: `h${Math.min(Math.max(this.level, 1), 6)}`, attributes: buildAttributes(), events: buildEvents(), children: [{ tag: '#text', textContent: this.content }] };
    }

}

